<template>
  <div class="personalPage">
     <div class="data padd clearfix">
         <img src="../../assets/avatar.jpg" alt="" class="avatar fl">
         <div class="center fl">
             <p class="name">Pro9ice</p>
             <p class="three_t_l">已绑定微信</p>
         </div>
         <span class="setting three_t_m fr" @click="$router.push('/personalCenter/persondata')">资料设置</span>
     </div>
     <div class="order clearfix padd" @click="$router.push('/personalCenter/myorders')">
         <span class="fl">我的订单</span>
         <span class="fr">></span>
     </div>
    <div class="mine padd">
        <ul>
            <li v-for="(item,index) in mine_list" :key="index" @click="$router.push(item.path)" class="clearfix">
                <span class="fl">{{item.name}}</span> 
                <span v-if="index!=0" class="fr">></span>
                <span v-if="index==0" class="fr yue">¥10.00</span>
            </li>
        </ul>
    </div>
    <div class="about padd">
        <ul>
            <li class="clearfix" @click="showConfirm=true">
                <span class="fl">联系客服</span>
                <span class="fr">></span>
            </li>
            <li class="clearfix">
                <span class="fl">关于得乐</span>
                <span class="fr">></span>                
            </li>
        </ul>
    </div>
    <confirm v-model="showConfirm" @on-confirm="call()" confirm-text="联系客服" title="得乐客服热线">
      <p style="text-align:center;">400-123-6666</p>  
    </confirm> 
    <a ref="tel" href="tel:400-123-6666" style="visibility: hidden;">400-123-6666</a>
  </div>
</template>

<script>
import { Confirm } from 'vux'

export default {
  name: "PersonalPage",
  data() {
    return {
      mine_list: [
        {
          name: "我的余额",
          path: '/personalCenter/personalPage'
        },
        {
          name: "地址管理",
          path: '/personalCenter/address'
        },
        {
          name: "我的收藏",
          path: '/personalCenter/mycollection'
        },
        {
          name: "我看过的",
          path: '/personalCenter/isseen'
        },
        {
          name: "附近的得乐 Café",
          path: '/home/nearby'
        },
        {
          name: "企业订购",
          path: '/personalCenter/enterprise'
        }
      ],
      showConfirm: false
    };
  },
  methods: {
    call(){
      this.$refs.tel.click()
    }
  },
  components: {
    Confirm
  }
};
</script>

<style scoped lang="less">
.personalPage {
  font-size: 0.16rem;
  background-color: #f2f2f2;
  padding-bottom: 0.6rem;
  .padd {
    padding: 0rem 0.27rem 0 0.28rem;
  }
  .data {
    height: 1rem;
    background-color: #fff;
    img {
      width: 0.6rem;
      height: 0.6rem;
      border-radius: 50%;
      margin: 0.2rem 0.2rem 0.2rem 0rem;
    }
    .center {
      .name {
        font-family: PingFangSC-Regular;
        font-size: 0.2rem;
        color: #191919;
        letter-spacing: 0;
        margin: 0.25rem 0 0.05rem 0rem;
      }
    }
    .setting {
      width: 0.6rem;
      height: 0.24rem;
      border: 0.01rem solid rgba(0, 0, 0, 0.5);
      border-radius: 0.03rem;
      text-align: center;
      line-height: 0.22rem;
      margin: 0.38rem 0rem;
    }
  }
  .order {
    background-color: #fff;
    height: 0.52rem;
    line-height: 0.52rem;
    margin: 0.1rem 0rem;
  }
  .mine {
    background-color: #fff;
    margin-bottom: 0.1rem;
    ul {
      li {
        height: 0.52rem;
        line-height: 0.51rem;
        border-bottom: 0.01rem solid #f2f2f2;
        .yue {
          font-family: PingFangSC-Regular;
          font-size: 0.16rem;
          color: #e7290f;
          letter-spacing: 0;
        }
      }
    }
  }
  .about {
    background-color: #fff;
    ul {
      li {
        height: 0.52rem;
        line-height: 0.51rem;
        border-bottom: 0.01rem solid #f2f2f2;
        &:nth-last-child(1) {
          border: none;
        }
      }
    }
  }
}
</style>



